.messages {
	margin-top: 20px;
	margin-bottom: 50px;
	font-size: 18px;
	font-weight: 700;
	color: #fff;
}

.search-people {
	margin-bottom: 20px;
}

.search-people-input {
	border: 1px solid #1b1b1b;
	min-height: 40px;
	width: 100%;
	font-size: 14px;
	padding-left: 15px;
	color: #bdc3d3;
	border-radius: 3px;
	background: #1b1b1b;
}

.people-search {
	position: absolute;
	top: 0;
	right: 15px;
	color: #fff;
	background: #fa2276;
	border: 0;
	height: 40px;
	padding: 0 20px;
	cursor: pointer;
	font-size: 14px;
	border-bottom-right-radius: 3px;
	border-top-right-radius: 3px;
}

