/* user */

.my-listing-dt-all-follow {
	border: 1px solid #1b1b1b;
	border-radius: 3px;
	text-align: center;
	padding: 20px 0 0;
	margin-top: 30px;
	background: #1b1b1b;
}

.my-lisiting-picy img {
	width: 70px;
	height: 70px;
	border-radius: 5px;
	border: 2px solid #fff;
	box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.2);
}

.my-listing-dt-all-follow h4 {
	font-size: 16px;
	font-weight: 500;
	color: #fff;
	margin: 15px 0 5px;
}

@media (min-width: 43.75em) h4 {
	line-height: 1.22222222;
}

.my-listing-dt-all-follow p {
	font-size: 14px;
	font-weight: 400;
	color: #bdc3d3;
	margin-bottom: 15px;
	padding: 0 10px;
	line-height: 26px;
}

.my-listing-dt-all-follow p i {
	top: -2px;
	bottom: 0;
	font-size: 14px;
	right: 5px;
	color: #fa2276;
}

.dashboard-main-dt-follow {
	width: 100%;
	text-align: center;
	margin: 30px 0;
}

.dashboard-main-dt-follow li {
	display: inline-block;
	padding: 0 15px;
	margin: 0 -1px;
}

.lisitng-dt2 ins {
	display: block;
	color: #fff;
	margin-bottom: 5px;
	font-size: 16px;
	font-weight: 500;
	text-decoration: none;
}

.lisitng-dt2 span {
	color: #bdc3d3;
	font-size: 12px;
	font-weight: 400;
}

[type=reset], [type=submit], button, html [type=button] {
	-webkit-appearance: none;
}

.folow-btn {
	width: 100%;
	height: 40px;
	background: #fa2276;
	color: #fff;
	border: 0;
	font-size: 14px;
	font-weight: 400;
	border-bottom-left-radius: 3px;
	border-bottom-right-radius: 3px;
}

.folow-btn:hover {
	background: #1b1b1b;
	border: 1px solid #fff;
	color: #fff;
	cursor: pointer;
}

[type=button]:not(:disabled), [type=reset]:not(:disabled), [type=submit]:not(:disabled), button:not(:disabled) {
	cursor: pointer;
}

.view-spinner {
	width: 100%;
	text-align: center;
	margin: 50px 0;
}